
// FILE: components/OverviewStats.tsx
import React from 'react'


type Student = {
student_id: number
name: string
class: string
comprehension: number
attention: number
focus: number
retention: number
assessment_score: number
engagement_time: number
}


export default function OverviewStats({ data }: { data: Student[] }) {
const avg = (key: keyof Student) => Math.round((data.reduce((s, r) => s + (r[key] as number), 0) / data.length) * 10) / 10


return (
<div className="bg-white rounded-2xl p-4 shadow flex gap-4 overflow-x-auto">
<div className="flex-1">
<div className="text-sm text-gray-500">Students</div>
<div className="text-2xl font-bold">{data.length}</div>
</div>


<div className="flex-1">
<div className="text-sm text-gray-500">Avg Assessment</div>
<div className="text-2xl font-bold">{avg('assessment_score')}</div>
</div>


<div className="flex-1">
<div className="text-sm text-gray-500">Avg Comprehension</div>
<div className="text-2xl font-bold">{avg('comprehension')}</div>
</div>


<div className="flex-1">
<div className="text-sm text-gray-500">Avg Attention</div>
<div className="text-2xl font-bold">{avg('attention')}</div>
</div>


<div className="flex-1">
<div className="text-sm text-gray-500">Avg Retention</div>
<div className="text-2xl font-bold">{avg('retention')}</div>
</div>
</div>
)
}